import React from 'react';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import imgBlog from '../../assets/imgBlog.jpg';

const blog = (props) => {
    return (
        <>
            <Header img={imgBlog} title="Blog des Thés Fleuris"  />
            cards
            <Footer />
        </>
    )
}

export default blog;